/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/

/* Links */

a {
	color: $color__link;

	&:visited {
		color: $color__link-visited;
	}
	&:hover,
	&:focus,
	&:active {
		color: $color__link-hover;
	}
	&:focus {
		outline: thin dotted;
	}
	&:hover,
	&:active {
		outline: 0;
	}
}

/* Menus */


// Banner Menu

.logo {
  background:
  url(../../images/logos/inhabitent-logo-tent.svg) no-repeat;
  position: absolute;  
  background-size: 50px 33px;
  height: 100px;
  width: 100px;
  left: 50px;
  top: 10px;
}

.main-navigation a {
  color:white; 
  padding: 1rem;
}

.main-navigation {
	
	clear: both;
	display: block;
	display: flex;
	justify-content: flex-end;
  position: fixed;
	background-color: $color__background-header;  
	border-bottom: $color__border;
	z-index: 3;
	width: 100%;
	

	ul {
		display: none;
		list-style: none;
		margin: 0;
		padding-left: 0;
		justify-content: space-between;

		ul {
			box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
			float: left;
			position: absolute;
			top: $base__space-unit;
			left: -999rem;
			z-index: 99999;

			ul {
				left: -999rem;
				top: 0;
			}

			li {
				&:hover > ul,
				&.focus > ul {
					left: 100%;					
				}
			}

			a {
				width: 200px;
				padding-left: 1.5rem;
			}

			// :hover > a,
			// .focus > a {
			// }

		// 	a:hover,
		// 	a.focus {
		// 	}
		// }

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}

	li {
		float: left;
		position: relative;
		text-transform: uppercase;

		&:hover > a,
		&.focus > a {
			color: $color__brand-purple;
		}
	}

	a {
		display: block;
		text-decoration: none;
		color: $color__brand-green;
	}

// 	.current_page_item > a,
// 	.current-menu-item > a,
// 	.current_page_ancestor > a {
// 	}
	}
}	
.search-bar {
	padding: 13px 40px 0 0;
}
.fa-search {
	color:$color__brand-green;
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;	
}

@media screen and (min-width: 768px) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: block;
	}
}

.comment-navigation,
.posts-navigation,
.post-navigation {

	.site-main & {
		margin: 0 0 $base__space-unit;
		overflow: hidden;
	}

	.nav-previous {
		float: left;
		width: 50%;
	}

	.nav-next {
		float: right;
		text-align: right;
		width: 50%;
	}
}

